import { createElement } from "react"
import { INLINE_ATTRIBUTION_REGEX } from "@rp/common/utils"
import { createChunkMap } from "@rp/react/common/hooks"
import { OnUserInteractionProps } from "../ChatResponse"
import { InlineAttribution } from "./InlineAttribution"

type ChunkMap = ReturnType<typeof createChunkMap>["chunkMap"]
type ExternalMap = ReturnType<typeof createChunkMap>["externalMap"]

type Props = {
  chunkMap: ChunkMap
  externalMap: ExternalMap
  disabled?: boolean
  onUserInteraction: (props: OnUserInteractionProps) => void
}

const inlineAttributionRenderer =
  ({ chunkMap, externalMap, disabled, onUserInteraction }: Props) =>
  ({ children, className }: { className?: string; children?: React.ReactNode }) => {
    if (typeof children === "string" && children.match(INLINE_ATTRIBUTION_REGEX)) {
      return (
        <InlineAttribution
          refId={children}
          chunkMap={chunkMap}
          externalMap={externalMap}
          disabled={disabled}
          onUserInteraction={onUserInteraction}
        />
      )
    }

    const code = createElement("code", className ? { className: className } : null, children)
    return createElement("code", {}, code)
  }

export { inlineAttributionRenderer }
